import React from 'react'
import { Link } from 'react-router-dom'
import { Container, Row, Col } from 'react-bootstrap'
import LogoHeader from '../../../../components/Header/Desktop/LogoHeader/LogoHeader'
import LoginForm from '../Components/LoginForm'
import GoogleOauth from '../Components/GoogleOAuth'
import Header from '../../../../components/Header/Header'

function DesktopLogin() {
    return (
        <>
            <Header />
            <Container className='py-3 py-sm-4 py-md-5'>
                <Row>
                    <Col lg={7}>
                        <div className="text-center mb-4 font-weight-bold">
                            Belum punya akun? <Link to="/register" className="text-signature"><u>Daftar Sekarang</u></Link>
                        </div>
                        <LoginForm />
                        <div className="text-center mb-7">
                            <Link to="/forgot-password" className="text-signature">
                                Lupa password?
                            </Link>
                        </div>
                        <div className="text-grey text-center">
                            Atau masuk dengan
                        </div>
                        <div className="d-flex justify-content-center">
                            <GoogleOauth />
                        </div>
                    </Col>
                </Row>
            </Container>
        </>
    )
}

export default DesktopLogin